.authInput{
    padding: 5px;
    margin-left: 5px;
    margin-bottom: 10px;
    margin-top: 5px;
    border-radius: 5px;
    border: none;
    outline: none;
}

.authRemember{
    margin-left: 5px;
    max-width: 20px;
}

.authBtn{
    margin-top: 10px;
    padding: 5px 10px;
    width: 182px;
    text-transform: uppercase;
    margin-left: 5px;
    border-radius: 5px;
    border: none;
    outline: none;
    cursor: pointer;
}